<style>
body {
  margin: 0;
}

div {
  box-sizing: border-box;
}

.black-bg {
  width: 100%;
  height: 50%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  padding: 20px;
}

.white-bg {
  width: 80%;
  background: white;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  padding: 20px;
}
</style>
<template>
  <div class="black-bg" v-if="updateModal == true">
    <div class="white-bg">
      <form @submit.prevent="handleUpdate">
        <p>
            <h3 style="text-align: center">수정</h3>
          <label>A/S일자</label> :
          <input type="text" v-model="updateData.check_date">
        </p>
        <p>
          <label>증상</label> :
          <input type="text" v-model="updateData.as_cause">
        </p>
        <p>
          <label>조치내용</label> :
          <input type="text" v-model="updateData.as_action">
        </p>
        <p style="text-align: center;">
        <button type="submit">수정</button>
        <button type="button" @click="closeModal">닫기</button>
      </p>
      </form>
    </div>
  </div>
</template>
<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  updateModal: Boolean,
  serial_no: String,
  selectedRow: Object,
});

const selectedData = ref({...props.selectedRow});
const updateData = ref({
  serial_number: '',
  check_date: '',
  as_cause: '',
  as_action: '',
});

watch(() => props.selectedRow, (newValue) => {
  selectedData.value = { ...newValue };
  updateData.value = {
    index_num: newValue.r_repair_index || '',
  check_date: newValue.r_check_date || '',
  as_cause: newValue.r_as_cause || '',
  as_action: newValue.r_as_action || '',
};
  console.log(selectedData.value)
  console.log(updateData.value)
});

const emit = defineEmits(['update', 'close']);

const closeModal = () =>{
  emit('close');
}

const handleUpdate = async () =>{
  try {
            const response = await fetch('/api/update_repair_history', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(updateData.value),
            });
            console.log(response)
            if (!response.ok) {
                throw new Error('Failed to insert item');
            }

            const result = await response.json();
            emit('update')
            emit('close')
            console.log('Successfully inserted:', result);
        } catch (error) {
            console.error('Error:', error);
        }
}
</script>