<style scoped>
.content {
  padding: 1rem;
  background-color: #f9f9f9;
}

.inline {
  display: flex;
}

.insert-button {
  margin-left: auto;
  height: 50%;
}
</style>

<template>
  <div class="content">
    <p class="inline">
    <h2>사용자 기록</h2>
    <button class="insert-button" @click="inputModal = !inputModal">등록</button>
    </p>
    <UserHistoryInputModal :inputModal="inputModal" :serial_no="serial_no" @update="refreshData"
      @close="inputModal = false" />
    <UserHistoryUpdateModal :selectedRow="selectedRow" :updateModal="updateModal" @update="refreshData"
      @close="updateModal = false" />
    <table>
      <thead>
        <tr>
          <th>번호</th>
          <th>사용자 기록</th>
          <th>사용 시작/종료</th>
          <th>날짜</th>
          <th>수정/삭제</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for=" (list,i) in userHistoryData" :key="list.h_used_index">
          <td>{{ i+1}}</td>
          <td>{{ list.h_user_name }}</td>
          <td>{{ list.h_use_state }}</td>
          <td>{{ list.h_use_date }}</td>
          <td style="width: 100px;">
            <button class="update-button" @click="openUpdateModal(list)">수정</button>
            <button @click="deleteUserHistoryData(list.h_used_index)">삭제</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>


</template>

<script setup>
import { onMounted, ref, watch } from 'vue';
import UserHistoryInputModal from '../modal/UserHistoryInputModal.vue';
import UserHistoryUpdateModal from '../modal/UserHistoryUpdateModal.vue';


const props = defineProps({
  serialNum: String,
})

const serial_no = props.serialNum;
const userHistoryData = ref([])
const inputModal = ref(false);
const updateModal = ref(false);
const selectedRow = ref(null);

//상세정보 데이터 가져오기
const detailUserHistory = async (serial_no) => {
  try {
    const response = await fetch(`/api/read_user_history/${serial_no}`);
    if (!response.ok) {
      throw new Error('서버에서 데이터를 가져오지 못했습니다.');
    }
    const result = await response.json();
    userHistoryData.value = result;
  } catch (error) {
    console.error('데이터를 가져오는 중 오류가 발생했습니다:', error);
  }
};
//선택 데이터 삭제
const deleteUserHistoryData = async (index) => {
  if (window.confirm("정말 삭제하시겠습니까?")) {
    try {
      const response = await fetch('/api/delete_user_history', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ index_num: index }),
      });

      if (!response.ok) {
        throw new Error('Failed to delete item');
      }

    } catch (error) {
      console.error('Error:', error);
    }
    refreshData();
  } else {
    return;
  }
};

const openUpdateModal = (row) => {
  selectedRow.value = row;
  updateModal.value = !updateModal.value;
};

const refreshData = () => {
  detailUserHistory(serial_no);
}

onMounted(() => {
  detailUserHistory(serial_no);
});
</script>