<template>
    <div>
        <form class="container" @submit.prevent="handleUpdate">
            <input type="file" accept="image/png, image/jpeg, image/jpg" @change="handleFileName"/>
            <label>시리얼번호</label>
            <input type="text" v-model="updateData.serial_number" readonly>
            <label>종류</label>
            <selectComponent :optionType="'item'" v-model="updateData.item_name" style="width: 100%;" />
            <label>모델명</label>
            <input type="text" v-model="updateData.model_name">
            <label>제조사</label>
            <input type="text" v-model="updateData.manufacturer">
            <label>구입일자</label>
            <input type="text" v-model="updateData.bought_date">
            <label>구매처</label>
            <input type="text" v-model="updateData.buy_at">
            <label>현사용자</label>
            <selectComponent :optionType="'employee'" v-model="updateData.employee_name" style="width: 100%;" />
            <label>상태</label>
            <selectComponent :optionType="'status'" v-model="updateData.status_name" style="width: 100%;" />
            <label>위치</label>
            <selectComponent :optionType="'location'" v-model="updateData.location_name" style="width: 100%;" />
            <label>폐기여부</label>
            <select v-model="updateData.dispose" style="width: 100%;">
                <option value=false>X</option>
                <option value=true>O</option>
            </select>
            <label>특이사항</label>
            <textarea type="text" v-model="updateData.etc"></textarea>
            <br>
            <p>
                <button type="submit">입력</button>
                <button @click="goBack">뒤로가기</button>
            </p>
        </form>

    </div>
</template>
<script setup>
import { ref, watch, onMounted } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import selectComponent from '../components/selectComponent.vue'
const route = useRoute();
const router = useRouter();

const urlParam = route.params.id;
const detail = JSON.parse(route.query.data || '{}')
const fileName =ref('');

const handleFileName = () =>{
    const file = event.target.files[0];
  if (file) {
    fileName.value = file.name;
    updateData.value.image_url =file.name;
  } else {
    fileName.value = detail.d_image_url||'';
  }
  console.log(fileName.value)
};

const updateData = ref({
    serial_num: urlParam,
    image_url: detail.d_image_url || '',
    serial_number: detail.d_serial_number || '',
    item_name: detail.d_item_code || '',
    status_name: detail.d_status_code || '',
    model_name: detail.d_model_name || '',
    manufacturer: detail.d_manufacturer || '',
    bought_date: detail.d_bought_date || '',
    buy_at: detail.d_buy_at || '',
    dispose: false,
    employee_name: detail.d_employee_code || '',
    location_name: detail.d_location_code || '',
    etc: detail.d_etc || '',
});

const handleUpdate = async () => {
    if (updateData.value.serial_number == '') {
        alert('시리얼번호를 입력해주세요')
        return;
    }
    if (updateData.value.item_name == '') {
        alert('종류를 선택해주세요')
        return;
    }
    if (updateData.value.model_name == '') {
        alert('모델명을 입력해주세요')
        return;
    }
    if (updateData.value.manufacturer == '') {
        alert('제조사를 입력해주세요')
        return;
    }
    if (updateData.value.bought_date == '') {
        alert('구입일자를 입력해주세요')
        return;
    }
    if (updateData.value.buy_at == '') {
        alert('구매처를 입력해주세요')
        return;
    }
    if (updateData.value.status_name == '') {
        alert('상태를 선택해주세요')
        return;
    }
    if (updateData.value.location_name == '') {
        alert('위치를 선택해주세요')
        return;
    }
    try {
        const response = await fetch('/api/update_fixtures', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(updateData.value),
        });
        if (!response.ok) {
            throw new Error('Failed to update item');
        }

        const result = await response.json();
        console.log('Successfully inserted:');
    } catch (error) {
        console.error('Error:', error);
    }
    router.push({name: 'ListDetail', params:{ id : urlParam}});
};


</script>