<style>
.selectbar-area {
    display: flex;
}

.insert-button {
    margin-left: auto;
}
</style>
<template>
    <div class="container">
        <p class="selectbar-area">
            <selectComponent :optionType="'item'" @optionSelected="handleOptionSelected" />
            <selectComponent :optionType="'status'" @optionSelected="handleOptionSelected" />
            <selectComponent :optionType="'location'" @optionSelected="handleOptionSelected" />
            <button class="insert-button" @click="moveToCreate">비품등록</button>
        </p>
        <br />
        <table border="1">
            <thead>
                <th>번호</th>
                <th>종류</th>
                <th>시리얼넘버</th>
                <th>상태</th>
                <th>위치</th>
                <th>현사용자</th>
            </thead>
            <tbody>
                <tr v-for="list in listData" :key="list.id" @click="moveToDetails(list)">
                    <td>{{ list.list_index }}</td>
                    <td>{{ list.list_item_code }}</td>
                    <td>{{ list.list_serial_number }}</td>
                    <td>{{ list.list_status_code }}</td>
                    <td>{{ list.list_location_code }}</td>
                    <td>{{ list.list_employee_code }}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script setup>
import { ref, watch, onMounted } from 'vue';
import selectComponent from '../components/selectComponent'
import { useRouter } from 'vue-router';
const listData = ref([]);
const router = useRouter();
let option1 = ref(null);
let option2 = ref(null);
let option3 = ref(null);

const moveToDetails = (list) => {
    router.push({name: 'ListDetail', params:{ id : list.list_serial_number}});
}

const moveToCreate = () =>{
    router.push(`/create/`)
}

const handleOptionSelected = (optionValue) => {
    if (optionValue.id === 'item') {
        option1.value = optionValue.value
        console.log(option1)
    } else if (optionValue.id === 'status') {
        option2.value = optionValue.value
    } else if (optionValue.id === 'location') {
        option3.value = optionValue.value
    }
};

const filter = async () => {
    try {
        const param1 = option1.value;
        const param2 = option2.value;
        const param3 = option3.value;
        
        if (param1 === '선택') {
            option1.value = null
        }
        if (param2 === '선택') {
            option2.value = null
        }
        if (param3 === '선택') {
            option3.value = null
        }

        const response = await fetch(`/api/filter_list?option1=${param1}&option2=${param2}&option3=${param3}`);
        if (!response.ok) {
            throw new Error('서버에서 데이터를 가져오지 못했습니다.');
        }
        const result = await response.json();
        listData.value = result;
    } catch (error) {
        console.error('데이터를 가져오는 중 오류가 발생했습니다:', error);
    }
};
onMounted(()=>{
    filter();
})
watch([option1, option2, option3], () => {
    filter();
});

</script>