<style scoped>
.table-area {
    display: grid;
    place-items: center;
}
.status {
    width: 150px;
}
.detail-button{
    text-align: center;
}
</style>

<template>
    <div class="container">
        <imageComponent v-if="detail.d_image_url" :imageUrl="detail.d_image_url"/>
        <br />
        <div class="table-area">
            <table class="status">
                <thead>
                    <tr>
                        <th>상태</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>{{ detail.d_status_code }}</td>
                    </tr>
                </tbody>
            </table>
            <br />
            <table>
                <thead>
                    <tr>
                        <th>시리얼넘버</th>
                        <th>종류</th>
                        <th>모델명</th>
                        <th>제조사</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>{{ detail.d_serial_number }}</td>
                        <td>{{ detail.d_item_code }}</td>
                        <td>{{ detail.d_model_name }}</td>
                        <td>{{ detail.d_manufacturer }}</td>
                    </tr>
                </tbody>
            </table>
            <br />
            <table>
                <thead>
                    <tr>
                        <th>구입일</th>
                        <th>구매처</th>
                        <th>폐기여부</th>
                        <th>사용자</th>
                        <th>위치</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>{{ detail.d_bought_date }}</td>
                        <td>{{ detail.d_buy_at }}</td>
                        <td>{{ detail.d_dispose }}</td>
                        <td>{{ detail.d_employee_name }}</td>
                        <td>{{ detail.d_location_name }}</td>
                    </tr>
                </tbody>
            </table>
        </div>
        <p class="detail-button">
            <br />
            <button @click="moveToUpdate">수정</button>
            <button @click="moveToList">목록</button>
            <button @click="handleDelete">삭제</button>
        </p>
        <p>
            <button @click="changeTab(1)">사용자기록</button>
            <button @click="changeTab(2)">A/S기록</button>
            <button @click="changeTab(3)">특이사항</button>
        </p>
        <TabComponent :activeTab="activeTab" :detail="detail" :serialNum="urlParam" />
    </div>
</template>
<script setup>
import { ref, watch, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router';
import TabComponent from '../components/TabComponent.vue';
import imageComponent from '../components/imageComponent.vue';


const route = useRoute();
const router = useRouter();
const urlParam = route.params.id;
const detail = ref({});
const activeTab = ref(1);

const detailData = async (urlParam) => {
    try {
        const response = await fetch(`/api/detail_info/${urlParam}`);
        if (!response.ok) {
            throw new Error('서버에서 데이터를 가져오지 못했습니다.');
        }
        const result = await response.json();
        detail.value = result[0];
        console.log(detail.value)
    } catch (error) {
        console.error('데이터를 가져오는 중 오류가 발생했습니다:', error);
    }
};

const deleteData = async ()=>{
        try {
            const response = await fetch('/api/delete_fixtures', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ serial_num : urlParam }),
            });

            if (!response.ok) {
                throw new Error('Failed to delete item');
            }

            const result = await response.json();
            console.log('삭제완료', result)
        } catch (error) {
            console.error('Error:', error);
        }
        moveToList();
    };


const moveToList = () => {
    router.push('/list')
}

const moveToUpdate = () => {
    router.push({
        name: 'ListUpdate',
        params: { id: urlParam },
        query : {data: JSON.stringify(detail.value)}
    });
}

const handleDelete = (e) =>{
        if(window.confirm("정말 삭제하시겠습니까?")){
           deleteData()
        } else {
            return;
        }
    }

const changeTab = (i) => {
    activeTab.value = i
}

onMounted(() => {
    detailData(urlParam);
})
</script>