<style>
body {
  margin: 0;
}

div {
  box-sizing: border-box;
}

.black-bg {
  width: 100%;
  height: 50%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  padding: 20px;
}

.white-bg {
  width: 80%;
  background: white;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  padding: 20px;
}
</style>
<template>
  <div class="black-bg" v-if="updateModal == true">
    <div class="white-bg">
      <form @submit.prevent="handleUpdate">
        <p>
            <h3 style="text-align: center">수정</h3>
          <label>사용자명</label> :
          <input type="text" v-model="updateData.user_code">
        </p>
        <p>
          <label>시작/종료</label> :
          <select v-model="updateData.use_state">
            <option disabled value="">선택</option>
            <option>사용시작</option>
            <option>사용완료</option>
            </select>
        </p>
        <p>
          <label>날짜</label> :
          <input type="text" v-model="updateData.use_date">
        </p>
        <p style="text-align: center;">
        <button type="submit">수정</button>
        <button type="button" @click="closeModal">닫기</button>
      </p>
      </form>
    </div>
  </div>
</template>
<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  updateModal: Boolean,
  serial_no: String,
  selectedRow: Object,
});

const selectedData = ref({...props.selectedRow});
const updateData = ref({
  used_index: '',
  user_code: '',
  use_state: '',
  use_date: '',
});

watch(() => props.selectedRow, (newValue) => {
  selectedData.value = { ...newValue };
  updateData.value = {
  used_index: newValue.h_used_index || '',
  user_code: newValue.h_user_name || '',
  use_state: newValue.h_use_state || '',
  use_date: newValue.h_use_date || '',
};
  console.log(selectedData.value)
  console.log(updateData.value)
});

const emit = defineEmits(['update', 'close']);

const closeModal = () =>{
  emit('close');
}

const handleUpdate = async () =>{
  try {
            const response = await fetch('/api/update_user_history', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(updateData.value),
            });
            console.log(response)
            if (!response.ok) {
                throw new Error('Failed to insert item');
            }

            const result = await response.json();
            emit('update')
            emit('close')
            console.log('Successfully inserted:', result);
        } catch (error) {
            console.error('Error:', error);
        }
}
</script>