<style>
select {
    margin: 0px 10px 0px 10px;
    width: 130px;
}
</style>
<template>
    <select id="itemSelect" @change="selectedOption">
        <option>
            선택
        </option>
        <option v-for="item in selectOption" :key="getKey(item)" :value="getKey(item)">
            {{ getName(item) }}
        </option>
    </select>
</template>
<script setup>
import { ref, watch, onMounted } from 'vue';
const props = defineProps({
    optionType: {
        type: String,
        required: true,
    },
    modelValue: {
        type: String,
        default: '',
    }
});

const emit = defineEmits(['optionSelected','update:modelValue']);

const selectOption = ref([]);
const localValue = ref(props.modelValue);

watch(()=>props.modelValue,(newValue)=>{
    localValue.value = newValue;
})

const selectedOption = (e) =>{
    const selectedValue = e.target.value;
    emit('update:modelValue', selectedValue)
    emit('optionSelected',{id: props.optionType, value: selectedValue});
}

const getOption = async () => {
    let url;
    if(props.optionType === 'item'){
           url ='/api/item_name';
        }else if(props.optionType === 'status'){
            url ='/api/status_name';
        }else if(props.optionType === 'location'){
            url ='/api/location_name';
        }else if(props.optionType === 'employee'){
            url ='/api/employee_name';
        } 
    try {
            const response = await fetch(url);
        if (!response.ok) {
            throw new Error('서버에서 데이터를 가져오지 못했습니다.');
        }
        const result = await response.json();
        selectOption.value = result;
    } catch (error) {
        console.error('데이터를 가져오는 중 오류가 발생했습니다:', error);
    }
};

const getKey = (item) =>{
    if(props.optionType === 'item'){
        return item.item_code
    }else if(props.optionType === 'status'){
        return item.status_code
    }else if(props.optionType === 'location'){
        return item.location_code
    }else if(props.optionType === 'employee'){
        return item.employee_code
    }
}
const getName = (item) =>{
    if(props.optionType === 'item'){
        return item.item_name
    }else if(props.optionType === 'status'){
        return item.status_name
    }else if(props.optionType === 'location'){
        return item.location_name
    }else if(props.optionType === 'employee'){
        return item.employee_name
    }
}

onMounted(() => {
    getOption();
})
</script>