<style>
body {
    margin: 0;
}

div {
    box-sizing: border-box;
}

.black-bg {
    width: 100%;
    height: 50%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    padding: 20px;
}

.white-bg {
    width: 80%;
    background: white;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    padding: 20px;
}
</style>
<template>
    <div class="black-bg" v-if="inputModal == true">
        <div class="white-bg">
            <form @submit.prevent="handleSubmit">
                <p>
                <h3 style="text-align: center">등록</h3>
                <label>A/S일자</label> :
                <input type="text" v-model="inputData.check_date">
                </p>
                <p>
                    <label>증상</label> :
                    <input type="text" v-model="inputData.as_cause">
                </p>
                <p>
                    <label>조치내용</label> :
                    <input type="text" v-model="inputData.as_action">
                </p>
                <p style="text-align: center;">
                    <button type="submit">등록</button>
                    <button type="button" @click="closeModal">닫기</button>
                </p>
            </form>
        </div>
    </div>
</template>
<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
    inputModal: Boolean,
    serial_no: String,
});

const serial_no = props.serial_no;
const inputData = ref({
    serial_number: serial_no,
    check_date: '',
    as_cause: '',
    as_action: '',
});

const emit = defineEmits(['update', 'close']);


const closeModal = () => {
    emit('close');
}

const handleSubmit = async () => {
    try {
        const response = await fetch('/api/insert_repair_history', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(inputData.value),
        });
        console.log(response)
        if (!response.ok) {
            throw new Error('Failed to insert item');
        }

        const result = await response.json();
        emit('update')
        emit('close')
        console.log('Successfully inserted:', result);
    } catch (error) {
        console.error('Error:', error);
    }
    inputData.value.check_date = '';
    inputData.value.as_cause = '';
    inputData.value.as_action = '';

}

// watch(inputData, () => {
//     console.log(inputData.value)
// }, {deep: true})
</script>