<style scoped>
.content {
  padding: 1rem;
  background-color: #f9f9f9;
}

.inline {
  display: flex;
}

.insert-button {
  margin-left: auto;
  height: 50%;
}
</style>

<template>
  <div class="content">
    <p class="inline">
    <h2>A/S 기록</h2>
    <button class="insert-button" @click="inputModal = !inputModal">등록</button>
    </p>
    <AsHistoryInputModal :inputModal="inputModal" :serial_no="serial_no" @update="refreshData"
      @close="inputModal = false" />
    <AsHistoryUpdateModal :selectedRow="selectedRow" :updateModal="updateModal" @update="refreshData"
      @close="updateModal = false" />
    <table>
      <thead>
        <tr>
          <th>번호</th>
          <th>A/S일자</th>
          <th>증상</th>
          <th>조치내용</th>
          <th>수정/삭제</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for=" (list,i) in repairHistoryData" :key="list.r_repair_index">
          <td>{{ i+1 }}</td>
          <td>{{ list.r_check_date }}</td>
          <td>{{ list.r_as_cause }}</td>
          <td>{{ list.r_as_action }}</td>
          <td style="width: 100px;">
            <button class="update-button" @click="openUpdateModal(list)">수정</button>
            <button @click="deleteUserHistoryData(list.r_repair_index)">삭제</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script setup>
import { onMounted, ref } from 'vue';
import AsHistoryInputModal from '../modal/AsHistoryInputModal.vue';
import AsHistoryUpdateModal from '../modal/AsHistoryUpdateModal.vue';

const props = defineProps({
  serialNum: String,
})
const serial_no = props.serialNum;
const repairHistoryData = ref([])
const inputModal = ref(false);
const updateModal = ref(false);
const selectedRow = ref(null);

const detailRepairHistory = async (serial_no) => {
  try {
    const response = await fetch(`/api/read_repair_history/${serial_no}`);
    if (!response.ok) {
      throw new Error('서버에서 데이터를 가져오지 못했습니다.');
    }
    const result = await response.json();
    repairHistoryData.value = result;
  } catch (error) {
    console.error('데이터를 가져오는 중 오류가 발생했습니다:', error);
  }
};

const deleteUserHistoryData = async (index) => {
  if (window.confirm("정말 삭제하시겠습니까?")) {
    try {
      const response = await fetch('/api/delete_repair_history', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ index_num: index }),
      });

      if (!response.ok) {
        throw new Error('Failed to delete item');
      }

    } catch (error) {
      console.error('Error:', error);
    }
    refreshData();
  } else {
    return;
  }
};

const openUpdateModal = (row) => {
  selectedRow.value = row;
  updateModal.value = !updateModal.value;
};

const refreshData = () => {
  detailRepairHistory(serial_no);
}

onMounted(() => {
  detailRepairHistory(serial_no);
})
</script>