<style>
body {
  margin: 0;
}

div {
  box-sizing: border-box;
}

.black-bg {
  width: 100%;
  height: 50%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  padding: 20px;
}

.white-bg {
  width: 80%;
  background: white;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  padding: 20px;
}
</style>
<template>
  <div class="black-bg" v-if="inputModal == true">
    <div class="white-bg">
      <form @submit.prevent="handleSubmit">
        <p>
        <h3 style="text-align: center">등록</h3>
        <label>사용자명</label> :
        <input type="text" v-model="inputData.user_code">
        </p>
        <p>
          <label>시작/종료</label> :
          <select v-model="inputData.use_state">
            <option disabled value="">선택</option>
            <option>사용시작</option>
            <option>사용완료</option>
          </select>
        </p>
        <p>
          <label>날짜</label> :
          <input type="text" v-model="inputData.use_date">
        </p>
        <p style="text-align: center;">
          <button type="submit">등록</button>
          <button type="button" @click="closeModal">닫기</button>
        </p>
      </form>
    </div>
  </div>
</template>
<script setup>
import { ref } from 'vue';

const props = defineProps({
  inputModal: Boolean,
  serial_no: String,
});

const serial_no = props.serial_no;
const inputData = ref({
  serial_number: serial_no,
  user_code: '',
  use_state: '',
  use_date: '',
});

const emit = defineEmits(['update', 'close']);


const closeModal = () =>{
  emit('close');
}

const handleSubmit = async () => {
  try {
    const response = await fetch('/api/insert_user_history', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(inputData.value),
    });
    console.log(response)
    if (!response.ok) {
      throw new Error('Failed to insert item');
    }

    const result = await response.json();
    emit('update')
    emit('close')
    console.log('Successfully inserted:', result);
  } catch (error) {
    console.error('Error:', error);
  }
  inputData.value.user_code = '';
  inputData.value.use_state = '';
  inputData.value.use_date = '';

}

// watch(inputData, () => {
//     console.log(inputData.value)
// }, {deep: true})
</script>