<style>
.image_area{
    display:flex;
    justify-content: center;
    
}
.detail_img{
    width: 550px;
    height: 300px;
    border: 1px solid black;
}
</style>
<template>
    <div class="image_area">
        <img class="detail_img" :src="image">
    </div>
</template>
<script setup>
import { computed } from 'vue';
const props = defineProps({
    imageUrl: {
        type: String,
        required: true,
    },
})
const image = computed(()=>{
    return `/${props.imageUrl}`
})
</script>
